<template>
  <v-container>
      <v-card
      class="mx-auto mt-2"
      max-width="400"
      v-for="item in partlist"
      elevation="1"
      shaped
      outlined
      >
         <v-img class="white--text align-end" height="200px" :src="item.cover">
            <v-card-title class="orange--text font-weight-bold" >{{item.name}}</v-card-title>
         </v-img>
         
         <v-card-subtitle class="pb-0">初级</v-card-subtitle>
         <v-card-actions>
            <v-btn color="orange" text>收藏</v-btn>
            <v-btn color="orange" text @click="godetail(item)">开始</v-btn>
         </v-card-actions>
      </v-card>
  </v-container>
</template>
<script>
export default {
 name:'PartList',
 computed:{
    partlist(){
       return this.$store.state.exercise.exerciselist[this.$route.query.id].partlist
    }
 } ,
 mounted() {
   //  console.log(this.$store.state.exercise.exerciselist[0].partlist);
 },
 methods: {
    godetail(a){
      this.$router.push({
         name:'detail',
         params:{data:JSON.stringify(a)}
      })
    }
 },

}
</script>

